import React, { useState } from "react";
// components
import ExperienceCard from "../components/experience/ExperienceCard";
import HeadingAnimate from "../components/animate/HeadingAnimate";
import LoadAnimate from "../components/animate/LoadAnimate";
// mocks
import { experience } from "../mock/experience";

// ----------------------------------------------------------------------

export default function Experience() {
  return (
    <>
      <div id="experience" />
      <section className="container mx-auto mt-12 text-center sm:mt-20">
        <HeadingAnimate amount={1}>
          <h2 className=" text-center font-lato text-3xl font-semibold text-primary-700 dark:text-cyan-400 sm:text-5xl">
            WORK EXPERIENCE
          </h2>
        </HeadingAnimate>

        <LoadAnimate amount={0}>
          <div className="container mx-auto my-5 flex flex-col items-center p-5 md:flex-col">
            {experience.map((item, i) => {
              return (
                <ExperienceCard {...item} key={"exp" + i}></ExperienceCard>
              );
            })}
          </div>
        </LoadAnimate>
      </section>
    </>
  );
}
