import React from 'react';
import { StackedCarousel, ResponsiveContainer } from 'react-stacked-center-carousel';
import Iconify from '../../components/Iconify';
import { data } from '../../mock/certification';

export default function CertificationCard(props) {
  const ref = React.useRef();
  const [centerSlideDataIndex, setCenterSlideDataIndex] = React.useState(0);
  const onCenterSlideDataIndexChange = (newIndex) => {
    setCenterSlideDataIndex(newIndex);
  };
  return (
    <div className="real w-full ">
      <ResponsiveContainer
        carouselRef={ref}
        render={(parentWidth, carouselRef) => {
          let currentVisibleSlide = 3;
          if (parentWidth <= 768) currentVisibleSlide = 1;
          return (
            <StackedCarousel
              ref={carouselRef}
              slideComponent={Card}
              slideWidth={parentWidth < 800 ? parentWidth - 60 : 560}
              carouselWidth={parentWidth}
              data={data}
              currentVisibleSlide={currentVisibleSlide}
              maxVisibleSlide={3}
              useGrabCursor
              onActiveSlideChange={onCenterSlideDataIndexChange}
            />
          );
        }}
      />

      <Pagination centerSlideDataIndex={centerSlideDataIndex} />
    </div>
  );
}
function Pagination(props) {
  return (
    <div className="mt-5 flex justify-center gap-3 md:mt-8">
      {data.map((_, index) => {
        const isCenterSlide = props.centerSlideDataIndex === index;
        return (
          <div
            key={'pageCar' + index}
            className={` h-2.5 w-2.5 rounded-full ${
              isCenterSlide ? 'bg-cyan-400 shadow-[0_0_8px_1px_#0a80ee85]' : 'bg-gray-300'
            }`}
          />
        );
      })}
    </div>
  );
}
export const Card = React.memo(function (props) {
  const { data, dataIndex } = props;
  const item = data[dataIndex];
  return (
    <div className="my-slide-component w-full select-none text-gray-700 dark:text-gray-400 ">
      <div
        className="border-1 box-border aspect-[4/3.2] w-full rounded-md border border-gray-300 bg-white p-2 text-left shadow-md dark:border-gray-700 dark:bg-[#192436] md:p-4"
        draggable={false}
      >
        <img src={item.image} className="h-[90%] w-full" draggable={false}></img>
        <div className="md: mt-2 flex items-center justify-center gap-2 gap-4 text-center text-sm sm:text-base md:mt-4">
          <div className="text-primary-700 dark:text-cyan-400">{item.title}</div>
          <div>( {item.from} ) </div>
          <div>
            <a
              href={item.link}
              target="_blank"
              rel="noreferrer"
              className="transform transition-all duration-200 hover:scale-110 hover:text-primary-600 dark:hover:text-primary-300"
            >
              <Iconify icon="ph:link-simple" />
            </a>
          </div>
        </div>
      </div>
    </div>
  );
});
