import React, { useRef } from "react";
// components
import Iconify from "../components/Iconify";
import HeadingAnimate from "../components/animate/HeadingAnimate";
import LoadAnimate from "../components/animate/LoadAnimate";
import CertificationCard from "../components/certification/CertificationCard";
// mocks
// ----------------------------------------------------------------------

export default function Certification() {
  const ref = useRef();
  return (
    <>
      <div id="certification" />
      <section className="container mx-auto mt-12 text-center sm:mt-20">
        <HeadingAnimate amount={1}>
          <h2 className="text-center font-lato text-3xl font-semibold text-primary-700 dark:text-cyan-400 sm:text-5xl">
            CERTIFICATION
          </h2>
        </HeadingAnimate>

        <LoadAnimate amount={0}>
          <div className=":mt-12 mt-8 w-full">
            <CertificationCard></CertificationCard>
          </div>
        </LoadAnimate>
      </section>
    </>
  );
}
