import React, { useRef, useState } from "react";
// components
import Iconify from "../components/Iconify";
import HeadingAnimate from "../components/animate/HeadingAnimate";
import LoadAnimate from "../components/animate/LoadAnimate";
import AnimatedLottie from "../components/Lottie";
import axios from "../utils/axios";
// mock
import { contactInfo } from "../mock/profile";
import contactAnimationData from "../mock/lottie/contact.json";

// ----------------------------------------------------------------------

export default function Contact() {
  const [isSending, setIsSending] = useState(false);

  const formRef = useRef();

  const sendEmail = async (e) => {
    e.preventDefault();
    try {
      setIsSending(true);
      const result = await axios.post("/contact", {
        name: formRef.current.name.value,
        email: formRef.current.email.value,
        message: formRef.current.message.value,
      });
      if (result) alert("Sent Successfully!");
    } catch (error) {
      alert("Sending failed!");
    } finally {
      setIsSending(false);
      formRef.current.message.value = "";
    }
  };
  return (
    <>
      <div id="contact" />
      <section className="container mx-auto mt-12 text-center sm:mt-20">
        <HeadingAnimate amount={1}>
          <h2 className="text-center font-lato text-3xl font-semibold text-primary-700 dark:text-cyan-400 sm:text-5xl">
            CONTACT ME
          </h2>
        </HeadingAnimate>
        <LoadAnimate amount={0}>
          <p className="mx-auto mt-5 text-base leading-relaxed opacity-80 md:w-1/2">
            {contactInfo.description}
          </p>
        </LoadAnimate>
        <LoadAnimate amount={0}>
          <div className="m-2 flex flex-col items-center md:flex-row">
            <div className="basis-1/2">
              <AnimatedLottie animationDataFile={contactAnimationData} />
            </div>
            <form ref={formRef} onSubmit={sendEmail} className="w-full">
              <div className="relative w-full p-2">
                <div className="absolute left-5 top-4 font-semibold text-primary-700 dark:text-cyan-400">
                  Name{" "}
                </div>
                <input
                  type="text"
                  id="name"
                  required
                  placeholder="Full Name"
                  name="user_name"
                  className="w-full rounded border border-primary-700/70 bg-primary-100/20 py-1 pl-16 pr-3 text-base leading-8 outline-none transition-colors duration-200 ease-in-out focus:ring-1 focus:ring-primary-700/70 dark:border-primary-300/50 dark:bg-primary-300/10 dark:focus:ring-primary-300/50"
                />
              </div>
              <div className="relative w-full p-2">
                <div className="absolute left-5 top-4 font-semibold text-primary-700 dark:text-cyan-400">
                  Email
                </div>
                <input
                  type="email"
                  id="email"
                  required
                  placeholder="email@example.com"
                  name="user_email"
                  className="w-full rounded border border-primary-700/70 bg-primary-100/20 py-1 pl-16 pr-3 text-base leading-8 outline-none transition-colors duration-200 ease-in-out focus:ring-1 focus:ring-primary-700/70 dark:border-primary-300/50 dark:bg-primary-300/10 dark:focus:ring-primary-300/50"
                />
              </div>
              <div className="w-full p-2">
                <textarea
                  placeholder="message here..."
                  id="message"
                  name="message"
                  required
                  className="h-32 w-full resize-none rounded border border-primary-700/70 bg-primary-100/20 py-1 px-3 text-base leading-8 outline-none transition-colors duration-200 ease-in-out focus:ring-1 focus:ring-primary-700/70 dark:border-primary-300/50 dark:bg-primary-300/10 dark:focus:ring-primary-300/50"
                />
              </div>
              <div className="flex w-full p-2">
                <button
                  type="submit"
                  disabled={isSending}
                  className="text-md mb-2 inline-flex w-full items-center justify-center rounded-lg border bg-primary-700 px-1 py-2.5 font-medium text-primary-50 hover:bg-primary-700/80 focus:outline-none focus:ring-2 dark:border-primary-700 dark:bg-primary-500 dark:hover:bg-primary-700 dark:focus:ring-primary-600"
                >
                  {isSending ? (
                    "Sending..."
                  ) : (
                    <>
                      Send
                      <Iconify classes="ml-2" icon="fluent:send-16-filled" />
                    </>
                  )}
                </button>
              </div>
            </form>
          </div>
          <div className="mt-4 flex w-full flex-col flex-wrap justify-center gap-2 border-t border-neutral-700/50 px-2 py-6 text-center dark:border-neutral-300/50 md:flex-row md:gap-4">
            {contactInfo.contacts.map((item, i) => {
              return (
                <div
                  key={"contactInfo" + i}
                  className="border-1 flex w-full items-center justify-start gap-4 rounded border border-primary-700/70 bg-primary-100/20 p-4 text-left  text-sm dark:border-primary-300/50 dark:bg-primary-300/10 md:w-[31.5%]"
                >
                  <Iconify
                    classes="text-4xl text-primary-700 dark:text-cyan-400 flex-none "
                    icon={item.icon}
                  />
                  <div>{item.data}</div>
                </div>
              );
            })}
          </div>
        </LoadAnimate>
      </section>
    </>
  );
}

Contact.propTypes = {};
