import React from 'react';
import Iconify from '../Iconify';
import XpItem from './XpItem';

// ----------------------------------------------------------------------

export default function ExperienceCard({ duration, position, company, logo, descriptions, photo }) {
  return (
    <div className="relative h-full w-full">
      <div className="invisible absolute flex h-full w-[200px] flex-col items-center md:visible">
        <div className=" h-full w-1 bg-[#283247]"></div>
        <div className="my-5 h-10 w-10 flex-none rounded-full bg-[#0a80ee] shadow-[0_0_60px_15px_#0a80ee85]"></div>
        <div className=" h-full w-1 bg-[#283247]"></div>
      </div>
      <div className="mb-5 rounded-md bg-primary-800/10 p-5 md:my-3 md:mb-0 md:ml-[200px] md:flex md:flex-row-reverse md:items-start lg:px-10">
        <div className="w-full">
          <div className="mt-4 flex items-center">
            <img src={logo} alt={'logo'} className=" mr-5 h-[40px]"></img>
            <div className="text-base text-primary-300 md:text-xl">{company}</div>
          </div>
          <div className="mt-3 text-left text-base text-red-500 md:text-lg">{position}</div>
          <div className=" text-left text-sm text-primary-300 md:text-base">{duration}</div>
          <div className="py-4 text-left text-sm text-primary-800 dark:text-primary-400 md:text-base">
            {descriptions.map((description, id) => {
              return <XpItem key={'des' + id} paragraph={description} />;
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
