import React, { useState } from "react";
// components
import Iconify from "../components/Iconify";
import HeadingAnimate from "../components/animate/HeadingAnimate";
import LoadAnimate from "../components/animate/LoadAnimate";
import { aboutContact } from "../mock/profile";
// mocks

// ----------------------------------------------------------------------

export default function About() {
  return (
    <>
      <div id="about" />
      <section className="container mx-auto mt-12 text-center sm:mt-20">
        <HeadingAnimate amount={1}>
          <h2 className=" text-center font-lato text-3xl font-semibold text-primary-700 dark:text-cyan-400 sm:text-5xl">
            ABOUT ME
          </h2>
        </HeadingAnimate>

        <LoadAnimate amount={0}>
          <div className="container mx-auto my-5 flex flex-col items-start gap-5 p-5 md:flex-row md:gap-8">
            <div className="flex flex-none flex-col gap-3">
              <div className="border-1 text-md flex w-full items-center justify-start gap-4 rounded border border-red-700/70 bg-red-100/20 p-4  text-left dark:border-red-500/50 dark:bg-red-500/10 ">
                <Iconify
                  classes="text-4xl text-red-700 dark:text-red-500 flex-none "
                  icon="ph:user"
                />
                <div className="text-red-800 dark:text-red-500">
                  Dustin Hayate
                </div>
              </div>
              {aboutContact.map((item, i) => {
                return (
                  <div
                    key={"aboutContact" + i}
                    className="border-1 text-md flex w-full items-center justify-start gap-4 rounded border border-primary-700/70 bg-primary-100/20 p-4  text-left dark:border-primary-300/50 dark:bg-primary-300/10 "
                  >
                    <Iconify
                      classes="text-4xl text-primary-700 dark:text-cyan-400 flex-none "
                      icon={item.icon}
                    />
                    <div>{item.data}</div>
                  </div>
                );
              })}
            </div>
            <div className="flex flex-col gap-3 text-left text-lg sm:text-xl ">
              <div>
                I graduated from Osaka Institute of Technology and I got a
                bachelor's degree in Computer Science. I have both experience in
                working in office and remotely. My major skills are React, Node,
                Solidity and Blockchain technology.
              </div>
              <div>
                In the meantime, I worked on 2 companies and developed several
                projects including Financial and E-commerce and so on, and
                gained a good experience and skills of web development. And I
                received good reviews from several clients and put up my name on
                team section of websites.
              </div>
              <div>
                It can be said that the strengths that I can see are the
                simultaneous guarantee of project speed and quality, accurate
                time management, and sincerity.
              </div>
              <div>
                I will work harder and harder for your company with all my heart
                and ability. If there is anything I don't know, I will learn
                from my team members and I will focus all in the success of
                company.
              </div>
              <div>Let's work together! Regards!</div>
            </div>
          </div>
        </LoadAnimate>
      </section>
    </>
  );
}
