import React, { useState } from 'react';
import Iconify from '../Iconify';

const MAX_VISIBILITY = 3;

export const Card = ({ title, content }) => (
  <div className="card">
    <h2>{title}</h2>
    <p>{content}</p>
  </div>
);

export const Carousel = ({ children }) => {
  const [active, setActive] = useState(2);
  const count = React.Children.count(children);

  return (
    <div className="carousel relative w-3/4 md:w-1/2">
      {React.Children.map(children, (child, i) => (
        <div
          className="card-container"
          style={{
            '--active': i === active ? 1 : 0,
            '--offset': (active - i) / 3,
            '--direction': Math.sign(active - i),
            '--abs-offset': Math.abs(active - i) / 3,
            'pointer-events': active === i ? 'auto' : 'none',
            opacity: Math.abs(active - i) >= MAX_VISIBILITY ? '0' : '1',
            display: Math.abs(active - i) > MAX_VISIBILITY ? 'none' : 'block',
          }}
        >
          {child}
        </div>
      ))}
      {active > 0 && (
        <button className="absolute top-[45%] left-[-60px]" onClick={() => setActive((i) => i - 1)}>
          <Iconify icon={'material-symbols:arrow-circle-left-outline-rounded'} classes="text-5xl" />
        </button>
      )}
      {active < count - 1 && (
        <button className="absolute top-[45%] right-[-60px]" onClick={() => setActive((i) => i + 1)}>
          <Iconify icon={'material-symbols:arrow-circle-right-outline-rounded'} classes="text-5xl" />
        </button>
      )}
    </div>
  );
};
